/* ===================================================================
 * # testimonials
 *
 * ------------------------------------------------------------------- */
.s-testimonials {
  padding-top: var(--vspace-5);
  padding-bottom: var(--vspace-3_5);
  color: rgba(255, 255, 255, 0.8);
  background-color: var(--color-gray-19);
  overflow: hidden;
  position: relative;

  &__bg {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: url(../../image/testimonials-bg-3000.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &__bg::before {
    display: block;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: var(--color-gray-19);
    opacity: 0.8;
  }

  & h3 {
    margin-top: 0;
    color: white;
  }

  &__header,
  &__content {
    position: relative;
  }

  &__header {
    text-align: center;
    margin-bottom: var(--vspace-2);
  }

  &__content > .column:first-child {
    min-height: 0;
    min-width: 0;
  }

  /* ------------------------------------------------------------------- 
 * ## swiper style overrides
 * ------------------------------------------------------------------- */
  & .swiper-container {
    padding-bottom: var(--vspace-2_5);
  }

  &
    .swiper-container-horizontal
    .swiper-pagination-bullets
    .swiper-pagination-bullet {
    margin: 0 9px;
  }

  & .swiper-pagination-bullet {
    width: 6px;
    height: 6px;
    background: white;
    opacity: 0.4;
  }

  & .swiper-pagination-bullet-active {
    background: var(--color-1);
    opacity: 1;
  }
}

/* ------------------------------------------------------------------- 
 * ## testimonial slider 
 * ------------------------------------------------------------------- */
.testimonial-slider {
  position: relative;

  & p {
    font-weight: 400;
    font-size: 1.7rem;
    line-height: var(--vspace-1);
  }

  &__slide {
    position: relative;
  }

  &__author {
    display: inline-block;
    min-height: var(--vspace-2);
    margin-bottom: var(--vspace-0_5);
    padding-left: 8.8rem;
    position: relative;
  }

  &__avatar {
    width: var(--vspace-2);
    height: var(--vspace-2);
    border-radius: 100%;
    outline: none;
    position: absolute;
    top: 0;
    left: 0;
  }

  &__cite {
    display: block;
  }

  &__cite strong,
  &__cite span {
    font-style: normal;
  }

  &__cite strong {
    font-size: calc(var(--text-size) * 0.944);
    line-height: var(--vspace-0_5);
    color: white;
  }

  &__cite span {
    display: block;
    font-size: calc(var(--text-size) * 0.778);
    font-weight: 300;
    line-height: var(--vspace-0_5);
    color: rgba(255, 255, 255, 0.6);
  }
}

/* ------------------------------------------------------------------- 
 * responsive:
 * testimonials
 * ------------------------------------------------------------------- */
@media screen and (max-width: 800px) {
  .testimonial-slider__slide {
    text-align: center;
  }
}

@media screen and (max-width: 600px) {
  .testimonial-slider__author {
    padding-left: 6.8rem;
  }
}

@media screen and (max-width: 400px) {
  .testimonial-slider__author {
    padding-left: 0;
    margin-bottom: var(--vspace-0_5);
    min-height: 0;
    text-align: center;
  }
  .testimonial-slider__avatar {
    display: inline-block !important;
    margin: 0;
    position: static;
  }
}
